.opening {
  width: 100%;
  height: 100%;
  content: "";
  position: fixed;
  background: #fff;
  z-index: 10000;
  top: 0;
  left: 0;
}
.opening .op-logo {
  width: 29.3333333333%;
  max-width: 110px;
  position: absolute;
  top: 0;
  left: 50%;
  margin-top: 68.8%;
  transform: translateX(-50%);
}
.opening .parameter {
  content: "";
  position: absolute;
  width: 33.0666666667%;
  max-width: 124px;
  height: 1.5px;
  background-color: #d9d9d9;
  top: 0;
  left: 50%;
  margin-top: 95.7333333333%;
  transform: translateX(-50%);
}
.opening .parameter::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1.5px;
  background-color: #000;
  top: 60%;
  left: 0%;
  transform: translate(-50%, -50%) scale(0, 1);
  transform-origin: left;
}

@media screen and (min-width: 760px) {
  .opening .op-logo {
    width: 8.6111111111%;
    margin-top: 19.0972222222%;
  }
  .opening .parameter {
    width: 8.6111111111%;
    margin-top: 26.1111111111%;
  }
}
@keyframes parameter {
  0% {
    transform: scale(0, 1);
  }
  40% {
    transform: scale(0.5, 1);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes opHidden {
  0% {
    opacity: 1;
  }
  90% {
    opacity: 0;
  }
  100% {
    opacity: 0;
    display: none;
  }
}
@keyframes showAnime {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes mvOpning {
  0% {
    transform: scale(1.2, 1.2);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes itemImg {
  0% {
    transform: scale(1.1, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
