  background-color: #000;
  bottom: 0;
  right: 0;
  margin-bottom: 4%;
}
.profilebtn .plus2 {
  transform: rotate(90deg);
}
.profilebtn .profile {
  position: relative;
  font-size: 4.2666666667vw;
  text-align: left;
  width: 100%;
}
.profilebtn .profile::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background: #000;
  top: 0;
  left: 0;
  margin-top: 9%;
  transform-origin: left;
  transform: scale(0, 1);
}
