  animation: lineAnime 1s forwards;
}
.profilebtn:hover .profile::after {
  animation: hoverLineAnime 0.7s forwards;
}

@keyframes lineAnime {
  0% {
    transform-origin: left;
    transform: scale(1, 1);
  }
  1% {
    transform-origin: right;
    transform: scale(1, 1);
  }
  50% {
    transform-origin: right;
    transform: scale(0, 1);
  }
  51% {
    transform-origin: left;
    transform: scale(0, 1);
  }
  100% {
    transform-origin: left;
    transform: scale(1, 1);
  }
}
@keyframes hoverLineAnime {
  0% {
    transform-origin: right;
    transform: scale(1, 1);
  }
  50% {
    transform-origin: right;
    transform: scale(0, 1);
  }
  51% {
    transform-origin: left;
    transform: scale(0, 1);
  }
  100% {
    transform-origin: left;
    transform: scale(1, 1);
  }
}
