.modal-wrap {
  display: none;
  width: 100%;
  height: 100%;
  position: fixed;
  background: rgba(0, 0, 0, 0.7);
  z-index: 1000;
  overflow-y: scroll;
}

.modal {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1010;
  width: 100%;
  height: 100%;
  margin-top: 10%;
}
.modal .modal-content {
  position: absolute;
  top: 70%;
  left: 50%;
  background: #fff;
  width: 88%;
  height: fit-content;
  margin: 20% auto 10%;
  transform: translate(-50%, -50%);
}
.modal .modal-content .modal-close {
  position: relative;
  top: 10px;
  right: 0;
  width: 6.6666666667%;
  padding-bottom: 3.6363636364%;
  margin: 0px 10px 0 auto;
}
.modal .modal-content .modal-close::after, .modal .modal-content .modal-close::before {
  margin-top: 23%;
  content: "";
  width: 100%;
  height: 1px;
  position: absolute;
  top: 0;
  right: 0;
  background: #000;
  opacity: 0;
  transition: opacity 0.9s 0.45s ease-in;
}
.modal .modal-content .modal-close::after {
  transform: rotate(30deg);
}
.modal .modal-content .modal-close::before {
  transform: rotate(-30deg);
}
.modal .modal-content .works {
  width: 76.6666666667%;
