@use './_mixin' as *;
$pcw : 1440px;
$spw : 375px;

body{
    height: 100%;
}


img{
    width: 100%;
    height: 100%;
    object-fit: contain;
    
}

.ja{
    font-family: "dnp-shuei-ymincho-std", sans-serif;
    font-weight: 500;
    font-style: normal;
}

.en{
    font-family: "bodoni-urw", serif;
    font-weight: 400;
    font-style: normal;
}

.pc{
    display: none;
}
.sp{
    display: block;
}
@include pc {
    .pc{
        display: block;
    }
    .sp{
        display: none;
    }
}



  .fadein{
    opacity: 0;
    transition: opacity .9s ease;
  }

  .fadein.show{
    opacity: 1;
  }
  
