@use './_reset';
@use './_mixin' as *;
@use './_common';

// modeuls
@use './_modules/_test';


$pcw : 1440px;
$spw : 375px;



body {
    &.show {
        .opening {
            animation: opHidden 1.2s 1.4s forwards;

            .parameter {
                &::after {
                    animation: parameter 1s .2s forwards;
                }
            }
        }
        .top_img{
            overflow: hidden;
            img{
            transform: scale(1.2, 1.2);
            animation: mvOpning 4s 1s forwards;
            }
        }
        .mainttl{

            animation: showAnime 1s 3s forwards
        }
        .topline{
            animation: showAnime 1s 3.5s forwards
        }
        .sub{
            animation: showAnime 1s 3.5s forwards
        }
        .top_logo{
            animation: showAnime 1s 3s forwards
        }
    }

}

//  opening 
.opening {
    width: 100%;
    height: 100%;
    content: "";
    position: fixed;
    background: #fff;
    z-index: 10000;
    top: 0;
    left: 0;

    .op-logo {
        width: calc(110px / $spw) * 100%;
        max-width: 110px;
        position: absolute;
        top: 0;
        left: 50%;
        margin-top: calc(258px / $spw) * 100%;
        transform: translateX( -50%);
    }

    .parameter {
        content: "";
        position: absolute;
        width: calc(124px / $spw) * 100%;
        max-width: 124px;
        height: 1.5px;
        background-color: #d9d9d9;
        top: 0;
        left: 50%;
        margin-top: calc(359px / $spw) * 100%;
        transform: translateX( -50%);

        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 1.5px;
            background-color: #000;
            top: 60%;
            left: 0%;
            transform: translate(-50%, -50%) scale(0, 1);
            transform-origin: left;

        }
    }

}

@include pc {
    .opening {
        .op-logo {
            width: calc(124px / $pcw) * 100%;
            margin-top: calc(275px / $pcw) * 100%;
        }
        
        .parameter {
            width: calc(124px / $pcw) * 100%;
            margin-top: calc(376px / $pcw) * 100%;
        }


    }
}

@keyframes parameter {
    0% {
        transform: scale(0, 1);
    }

    40% {
        transform: scale(0.5, 1);
    }

    100% {
        transform: scale(1, 1);
    }
}

@keyframes opHidden {
    0% {
        opacity: 1;
    }

    90% {
        opacity: 0;
    }

    100% {
        opacity: 0;
        display: none;
    }
}

@keyframes showAnime {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}
@keyframes mvOpning {
    0%{
        transform: scale(1.2 , 1.2);
    }
    100%{
        transform: scale(1 , 1);
    }
}
@keyframes itemImg {
    0%{
        transform: scale(1.1 , 1.1);
    }
    100%{
        transform: scale(1 , 1);
    }
}

//  opening ↑



.contents {
    width: 100%;
    height: 100svh;
    overflow: scroll;
    background: #fff;
    scroll-snap-type: y proximity;

}

@include pc {
    .contents {
        width: 100%;
