        overflow: scroll;
        background: #fff;
        scroll-snap-type: y mandatory;
    }
}



.scroll{
    display: none;
}
.pc-link{
    display: none;
}
@include pc {
    .wrapper {
        display: flex;
        justify-content: space-between;
        position: relative;
        overflow: visible;

    }

    .PcContent {
        position: sticky;
        height: 100vh;
        display: block;
        width: 40.7638888889%;
    }

    .pc__ttl{
        position: absolute;
        top: 0;
        left: 0;
        margin-left: calc(84px / 587px) * 100%;
        margin-top: calc(60px / 587px) * 100%;

    }


    .pc_txt{
        position: absolute;
        bottom: 0;
        left: 0;
        width: calc(510px / 587px) * 100%;
        margin-left: calc(84px / 587px) * 100%;
        margin-bottom: calc(60px / 587px) * 100%;
    }

    .contents {
        width: calc(423px / 1440px) * 100%;
        margin-right: 12.2916666667%;
        max-width: 423px;
        height: 87.5vh;
        overflow: scroll;
        margin-top: 3.8%;
    }
    
    .contents::-webkit-scrollbar {
        display: none;
    }

    .scroll-bar-pc {
        display: block;
        position: fixed;
        top: 0;
        right: 0;
        margin-right: 9.3%;
        width: 1px;
        height: 12%;
        background: #fff;
        margin-top: calc(110px / $pcw) * 100%;
        
    }

    .scroll-bar-pc .current-pos {
        position: absolute;
        width: 2.5px;
        height: 7px;
        background: #fff;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
      }
    .scroll{
        display: block;
        line-height: 1;
        position: fixed;
        top: 0;
        right: 0;
        margin-top: 3.173611%;
        font-size: 1.1805555556vw;
        transform: rotate(90deg);
        transform-origin: left;
        color: #fff;
        margin-right: 6.7%;
        
    }
    .pc-link{
        display: flex;
        color: #fff;
        justify-content: space-between;
        width: calc(35px / $pcw) * 100%;
        span{
            display: block;
        }
        a{
            color: #fff;
        }
    }


}


