.lead {
    padding-top: calc(29px / $spw) * 100%;
    text-align: center;
    color: #6e6e6e;
    line-height: calc(24 / 12);
    font-size: calc(12px / $spw * 100vw);
}


.profilebtn {
    margin: calc(25px / $spw) * 100% auto calc(87px / $spw) * 100%;
    width: calc(100px / $spw) * 100%;
    position: relative;
    cursor: pointer;

    .plus1,
    .plus2 {
        position: absolute;
        content: "";
        width: calc(6px / 97px) * 100%;
        height: 1px;
        background-color: #000;
        bottom: 0;
        right: 0;
        margin-bottom: 4%;
    }

    .plus2 {
        transform: rotate(90deg);
    }

    .profile {
        position: relative;
        font-size: calc(16px / $spw * 100vw);
        text-align: left;
        width: 100%;

        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 1px;
            background: #000;
            top: 0;
            left: 0;
            margin-top: 9%;
            transform-origin: left;
            transform: scale(0,1);
        }
    }
    
    &.active{
        .profile {
            &::after {
                animation: lineAnime 1s forwards;
            }
        }
        
    }
    &:hover{
        .profile {
            &::after {
                animation: hoverLineAnime .7s forwards;
            }
        }   
    }

}

@keyframes lineAnime {
    0%{
        transform-origin: left;
        transform: scale(1,1);
    }
    1%{
        transform-origin: right;
        transform: scale(1,1);
    }
    50%{
        
        transform-origin: right;
        transform: scale(0,1);
    }
    51%{
        
        transform-origin: left;
        transform: scale(0,1);
    }
    

    100%{
        transform-origin: left;
        transform: scale(1,1);
    }
    
}
@keyframes hoverLineAnime {
    0%{ 
        transform-origin: right;
        transform: scale(1,1);
    }
    50%{
        transform-origin: right;
        transform: scale(0,1);
    }

    51%{
        transform-origin: left;
        transform: scale(0,1);
    }

    100%{
        transform-origin: left;
        transform: scale(1,1);
    }
    
}


