.modal-wrap {
    display: none;
    width: 100%;
    height: 100%;
    position: fixed;
    background: rgb(0, 0, 0, .7);
    z-index: 1000;
    overflow-y: scroll;

}

.modal {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1010;
    width: 100%;
    height: 100%;
    margin-top: 10%;

    .modal-content {
        position: absolute;
        top: 70%;
        left: 50%;
        background: #fff;
        width: calc(330px / $spw) * 100%;
        height: fit-content;
        margin: 20% auto 10%;
        transform: translate(-50%,-50%);


        .modal-close {
            position: relative;
            top: 10px;
            right: 0;
            width: 6.6666666667%;
            padding-bottom: 3.6363636364%;
            margin: 0px 10px 0 auto;


            &::after,
            &::before {
                margin-top: 23%;
                content: "";
                width: 100%;
                height: 1px;
                position: absolute;
                top: 0;
                right: 0;
                background: #000;
                opacity: 0;
                transition: opacity .9s .45s ease-in;
            }

            &::after {
                transform: rotate(30deg);
            }

            &::before {
                transform: rotate(-30deg);
            }
        }

        .works {
            width: calc(253px / 330px) * 100%;
            opacity: 0;
            transition: opacity .9s .45s ease-in;

            .art {
                width: calc(165px / 253px) * 100%;
                margin: 0 auto;
            }

            .artist {
                display: flex;
                width: 100%;
                margin-top: calc(40px / 253px) * 100%;
                gap: calc(14px / 253px) * 100%;
                vertical-align: middle;
                text-align: center;

                .photo {
                    width: calc(80px / 253px) * 100%;
                }

                .artistname {
                    padding-top: calc(13px / 253px) * 100%;
                    height: fit-content;
                    display: block;
                    text-align: left;

                    .by {
                        color: #6e6e6e;
                        font-size: calc(11px / $spw * 100vw);
                    }

                    .name {
                        font-size: calc(14px / $spw * 100vw);
                        line-height: calc(25 / 14);
                    }
                }
            }
        }

        .works_1 {
            padding-bottom: calc(35px / 253px) * 100%;
            border-bottom: #000 solid 1px;
            margin: 0 auto calc(40px / 253px) * 100%;
        }

        .works_2 {
            padding-bottom: calc(40px / 253px) * 100%;
            margin: 0 auto;
        }

        h4 {
            line-height: 1;
            margin: 0 auto calc(40px / $spw) * 100%;
            font-size: calc(18px / $spw * 100vw);
            text-align: center;
            padding-top: calc(40px / $spw) * 100%;
            opacity: 0;
            transition: opacity .9s .45s ease-in;
        }

        .pro-txt {
            font-size: calc(11px / $spw * 100vw);
            color: #6e6e6e;
            padding-top: calc(14px / 253px) * 100%;
            line-height: calc(16 / 11);
            opacity: 0;
            transition: opacity .9s .45s ease-in;

        }

        &.show {
            .modal-close {

                &::after,
                &::before {
                    opacity: 1;
                }
            }

            .works {
                opacity: 1;
            }

            h4 {
                opacity: 1;
            }

            .pro-txt {
                opacity: 1;
            }
        }
    }
}


