                        }
                    }
                }
            }

            h4 {
                font-size: calc(18px / $pcw * 100vw);
            }

            .pro-txt {
                font-size: calc(11px / $pcw * 100vw);
            }
        }
    }
}



.pc-side_link{
    width: calc(35px / $pcw) * 100%;
    position: absolute;
    bottom: 0;
    right: 0;   
    margin-right: calc(115px / $pcw) * 100%;
    margin-bottom: calc(64px / $pcw) * 100%;
    display: none;
}

@include pc {
    .pc-side_link{
    display: block;
    }
    .pro_link,
    .movie_link,
    .th_link{
        display: none;
    }
}

.pro_link,
.movie_link,
.th_link
{
    width: calc(35px / $pcw) * 100%;
    position: absolute;
    height: fit-content;
    bottom: 0;
    right: 0;
    margin-right: calc(115px / $pcw) * 100%;
    opacity: 1;
    display: block;
    a{
        display: block;
        width: 100%;
        height: auto;
        padding-bottom: calc(57px / 35px) * 100%;
    }

    
}

.pro_link{
    cursor: pointer;
    margin-bottom: 14%;
    padding-bottom: 5%;
    z-index: 10;
}
.movie_link{
    cursor: pointer;
    margin-bottom: 8%;
    padding-bottom: 6%;
    z-index: 100;
}
.th_link{
    margin-bottom: 3.236111%;
    a{
        padding-bottom: 200%;
    }
}


.fixed_bg{
    display: none;
    .bg{
        position: fixed;
        width: 100%;
        height: 100vh;
        object-fit: cover;
        top: 0;
        left: 0;
        display: block;
        opacity: 1;
    }
    .bg1{
        animation: bghidden 15s infinite;
        z-index: -5;
    }
    .bg2{
        z-index: -10;
    }
    .zara{
        position: fixed;
        width: 100%;
        top: 0;
        left: 0;
        z-index: -1;
        object-fit: cover;
        opacity: 0.08;
    }

}


@include pc {
    .fixed_bg{
        display: block;
    }
    
}

@keyframes bghidden {
    0%{
        opacity: 0;
    }
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}



  .modal-video {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1000000;
    cursor: pointer;
    display: none;

  }

  

  .modal-video-body{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }

  @include sp {

    .modal-video-body iframe{
        max-width: 290px;
        max-height: 200px;
    }
  }

